"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";

import { getUserData } from "@/lib/auth";
import { logoutOrganizer } from "@/lib/logout";
import { loginPage, organizerLoginPage } from "@/app/routes";
import { LABEL_LOGOUT } from "@/const-value/config-message/page";

import "./Navbar.css";

export default function Navbar() {
  const router = useRouter();

  const [mounted, setMounted] = useState(false);
  const [initial, setInitial] = useState("U");

  useEffect(() => {
    setMounted(true);
    const userData = getUserData();
    if (userData?.email) {
      setInitial(userData.email.charAt(0).toUpperCase());
    }
  }, []);

  if (!mounted) return null;

  const handleCreateEvent = () => {
    router.push(organizerLoginPage);
  };

  const handleSignup = () => {
    router.push(loginPage);
  };

  const handleLogout = () => {
    logoutOrganizer(); // clears token
    router.push(organizerLoginPage);
  };

  return (
    <nav className="nav-container">
      {/* LEFT */}
      <div className="nav-left">
        <img src="/images/logo.png" alt="logo" className="nav-logo" />
        <button className="nav-explore">Explore ‚ñº</button>
      </div>

      {/* CENTER */}
      <div className="nav-center">
        <div className="nav-search-box">
          <input
            type="text"
            placeholder="Search anything"
            className="search-input"
          />
        </div>

        <button className="nav-location-btn">üìç</button>

        <button className="nav-create" onClick={handleCreateEvent}>
          Create Event
        </button>

        <button className="nav-create" onClick={handleSignup}>
          Signup
        </button>
      </div>

      {/* RIGHT */}
      <div className="nav-right">
        <button onClick={handleLogout} className="logout-btn">
          {LABEL_LOGOUT}
        </button>

        <button className="nav-avatar-btn">
          <div className="nav-letter-avatar">{initial}</div>
        </button>
      </div>
    </nav>
  );
}
